import React from "react"
import { Grid } from "semantic-ui-react"
import DonwloadPdf from "./DownloadPDF"
import * as actions from "./actions"

const Options = ({ imgRef }) => (
  <Grid>
    <Grid.Row>
      <actions.Share />
    </Grid.Row>
    <Grid.Row>
      <DonwloadPdf imgRef={imgRef}>
        {download => <actions.DownloadPdf onClick={download} />}
      </DonwloadPdf>
    </Grid.Row>
    <Grid.Row>
      <actions.DesignAnotherSpace />
    </Grid.Row>
  </Grid>
)

export default Options
